
.container, section, article, .header-content-wrapper {
	@extend %container;
}

html {

}
html,body {
	margin : 0;
	padding : 0;
	width : 100vw;
	height : 100%;
	max-width : 100vw;
	min-width : 300px;
	background-color : #fff;
}
body {
	min-width : 100vw;
	min-height : 100vh !important;
	& > header:first-of-type {
		padding : 2em 0;
		width : 100%;
		margin : 0;
		background-color : whitesmoke;
	}
	& > footer:last-of-type {
		width            : 100%;
		margin           : 0;
		padding          : 2em 0;
		background-color : whitesmoke;
		vertical-align   : middle;
		text-align       : center;
		
		p {
			font-size   : small;
			text-align  : center;
			line-height : 1;
			margin      : 0 auto;
			padding     : 0;
			font-weight : 500;
			color       : gray;
		}
	}
	
}
main {
	margin : 0;
	width : 100%;
	padding : 1em 0;
	background-color : #fff;
	& > section {
		padding : 1em 0;
		margin-bottom : 2em;
	}
}
